import React from "react";
import "./App.css";
import Clock from "./Components/Clock";
import { DateTime } from "luxon";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      clocks: ["Asia/Singapore", "America/New_York", "Europe/London"],
      value: "",
    };
  }

  addClock = (ianaCode) => {
    this.setState({
      clocks: [ianaCode, ...this.state.clocks],
    });
    console.log(this.state.clocks);
  };

  handleChange = (event) => {
    this.setState({ value: event.target.value });
  };

  handleSubmit = (event) => {
    if (DateTime.now().setZone(this.state.value).isValid) {
      this.addClock(this.state.value);
      this.setState({
        value: "",
      });
    } else {
      this.setState({
        value: "Invalid Input!",
      });
    }
    event.preventDefault();
  };

  render() {
    const listItems = this.state.clocks.map((clock, index) => (
      <li key={index}>
        <Clock timeZone={clock} />
      </li>
    ));
    return (
      <div>
        <div className="header">
          <h1>World Clock</h1>
        </div>
        <form onSubmit={this.handleSubmit}>
          <label>
            <input
              type="text"
              value={this.state.value}
              placeholder="Input TZ Identifier Here To Add"
              onChange={this.handleChange}
            />
          </label>
        </form>
        <ul className="clockWall">{listItems}</ul>
      </div>
    );
  }
}

export default App;
